  .dashboard-grid .main .header-main {
    display: flex;
    justify-content: space-between;
    margin: 2rem;
    padding: 2rem;
    color: #eeeeee;
    background-color: #3a4750;
    background: linear-gradient(to left, #46515e, #303841);
    border-radius: 1rem;
    box-shadow: 0px 3px 8px #303841; }
